import { AsyncStorage } from "react-native";
import firebase from "./firebase/MyFirebase";
import * as cardstore from "../store/CradsStore";
const UpdateCardMiddleWare = (store)=>(next)=>(action)=>
{
  if(action.type!='cardUpdating')
  {
    return next(action);
  }
 next(action);
 firebase.database().ref('users/'+firebase.auth().currentUser.uid).once('value').then(function(snapshot)
 {
   let snap =  snapshot;

   snap.forEach(function(childSnapshot) {
    if(childSnapshot.val().id==action.payload.id)
    {
      firebase.database().ref('users/'+firebase.auth().currentUser.uid+"/posts"+action.payload.id).update(action.payload).then(function()
      {
         cardstore.default.dispatch(cardstore.UPDATE_CARD(action.payload));
      }).
      catch(err=>
        {
          console.log(err);
        })
    }
  })
 }).catch(err=>
  {
    next(action);
  })
}
export default  UpdateCardMiddleWare;