import {
  REGISTER_USER,
  LOGIN_USER,
  VALIDATE_USER,
  LOGIN_SUCCESS,
  LOGIN_FAILURE,
} from "../store/AuthenticationStore";
import { AsyncStorage } from "react-native";
import firebase from "./firebase/MyFirebase";
const TokenUser = (store) => (next) => (action) => {
  console.log("inside Token Middleware");
  let regState = {
    loggedIn: false,
    Registered: false,
    ValidUser: false,
    loader: false,
    Email: "",
    Password: "",
    Errors: "",
    Token: "",
    uid:'',
  };
  if (action.type != "Token") {
    return next(action);
  }
  next(action);
  fetch("https://us-central1-survey-55706.cloudfunctions.net/sendToken/Token", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
    },
    body: JSON.stringify({ uid: action.payload.Token }),
  })
    .then(function (res) {
      if (res.ok) {
        return res.json();
      } else {
        regState.loggedIn = false;
        regState.Errors = "Something Went Wrong";
        store.dispatch(LOGIN_FAILURE(regState));
      }
    })
    .then((result) => {
      AsyncStorage.setItem("token", result.token)
        .then(function () {
          regState.loggedIn = true;
          regState.Errors = "";
          store.dispatch(LOGIN_SUCCESS(regState));
        })
        .catch((err) => {
          regState.loggedIn = false;
          regState.Errors = "Something Went Wrong";
          store.dispatch(LOGIN_FAILURE(regState));
        });
    })
    .catch((err) => {
      regState.loggedIn = false;
      regState.Errors = "Something Went Wrong";
      store.dispatch(LOGIN_FAILURE(regState));
    });
};
export default TokenUser;
