import React from "react";
import {
  StyleSheet,
  Text,
  View,
  Dimensions,
  TouchableOpacity,
  TouchableWithoutFeedback,
} from "react-native";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import { Button } from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import AlarmIcon from "@material-ui/icons/Alarm";
import store, {
  ADD_CARD,
  DELETE_CARD,
  SELECT_CARD,
  UNSELECT_CARD,
} from "../../store/CradsStore";

export default function NotesCard(props) {
  const [cardstate, setcardsate] = React.useState(false);
  let alaram ;
   if(props.carddata.alaram)
  {
     alaram = <Typography
    color="textSecondary"
    style={{ fontSize: 10, marginTop: "5%", marginBottom: 0,paddingBottom:0 }}
    gutterBottom
  >
      <IconButton
  aria-label="search"
  color="inherit"
  style={{paddingRight:3,paddingLeft:0,paddingTop:0}}
  onClick={() => {

  }}
>
  <AlarmIcon style={{ color: "textSecondary",fontSize:15 ,paddingTop:8}} />
</IconButton>
   {props.carddata.alaram}
  </Typography>
  }else
  {
     alaram ="";
  }

  var combinestyle;
  if (!cardstate) {
    combinestyle = StyleSheet.flatten([cardStyle, disselectedstyle]);
  } else {
    combinestyle = StyleSheet.flatten([cardStyle, selectedStyle]);
  }
  React.useEffect(() => {
    setcardsate(props.carddata.css.iselected );
  });
  return (
    <TouchableWithoutFeedback
      style={{ width: "100%" }}
      onLongPress={() => {
        if (!cardstate) {
          props.start(true);
          props.select(1);
          store.dispatch(SELECT_CARD({ id: props.val }));
          setcardsate(true);
        }
      }}
      onPress={() => {
        if (props.startState) {
          if (cardstate) {
            store.dispatch(UNSELECT_CARD({ id: props.val }));
            setcardsate(false);
            props.select(-1);
            return;
          }
          if (props.startState) {
            store.dispatch(SELECT_CARD({ id: props.val }));
            setcardsate(true);
            props.select(1);
            return;
          }    
        }
        props.nav.navigate('Create',{id:props.carddata.id,title:props.carddata.title,content: props.carddata.content,alaram:props.carddata.alaram})
      }}
    >
      <View style={{ width: "100%" }}>
        <Card style={combinestyle}>
          <CardContent style={{paddingBottom:0}}>
            <Typography
              variant="h6"
              component="h6"
              style={{
                fontSize: 15,
                fontWeight: "600",
                wordBreak: "break-all",
              }}
            >
              {props.carddata.title}
            </Typography>
            <Typography
              color="textSecondary"
              style={{ fontSize: 13, width: "100%", wordBreak: "break-all" }}
              display={"block"}
            >
              {props.carddata.content}
            </Typography>
            <Typography
              color="textSecondary"
              style={{ fontSize: 10, marginTop: "20%", marginBottom: 0 }}
              gutterBottom
            >
             {props.carddata.creation}
            </Typography>

          { alaram }
           

          </CardContent>
        </Card>
      </View>
    </TouchableWithoutFeedback>
  );
}
let selectedStyle = {
  borderColor: "brown",
  borderStyle: "solid",
};
let disselectedstyle = {
  borderColor: "white",
  borderStyle: "solid",
};
const cardStyle = {
  card: {
    width: "100%",
    justifyContent: "center",
    alignItems: "center",
    marginBottom: 10,
  },
};
