
import { AsyncStorage } from "react-native";
import firebase from "./firebase/MyFirebase";
import * as cardstore from "../store/CradsStore";
const DeleteCardMiddleWare = (store)=>(next)=>(action)=>
{
    console.log('inside DeleteCard MiddleWare',action.payload);
    var  mystore = cardstore;
    if(action.type!='cardDeleted')
    {
        return next(action);
    }
    next(action);
    let newcards = cardstore.default.getState().filter(card=>card.css.iselected === false);
    firebase.database().ref('users/'+firebase.auth().currentUser.uid).set(newcards).then(function()
    {
        cardstore.default.dispatch(cardstore.DELETING_CARD());
    }).catch(err=>
        {
            next(action);
        })
 
}
  export default DeleteCardMiddleWare;