import React from "react";
import {
  StyleSheet,
  Text,
  View,
  Dimensions,
  TouchableOpacity,
  TouchableWithoutFeedback,
  Platform
} from "react-native";
import AppBar from "@material-ui/core/AppBar";
import ToolBar from "@material-ui/core/Toolbar";
import MenuIcon from "@material-ui/icons/Menu";
import SearchIcon from "@material-ui/icons/Search";
import IconButton from "@material-ui/core/IconButton";
import InputBase from "@material-ui/core/InputBase";
import { makeStyles } from "@material-ui/core/styles";
import ArrowBack from "@material-ui/icons/ArrowBack";
import CloseIcon from "@material-ui/icons/Close";
import NotesCard from "../Crad/Card";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import { Button } from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import store, {
  ADD_CARD,
  DELETE_CARD,
  SELECT_CARD,
  UNSELECT_CARD,
} from "../../store/CradsStore";

export default function search(props) {
  const [searchvalue, setSearch] = React.useState("");
  const [cardarray, setCardArray] = React.useState([]);
  const classes = useStyles();
  React.useEffect(() => {
    setCardArray(store.getState());

  }, []);
  store.subscribe(() => {
    setCardArray(store.getState());
   
  });
  return (
    <View style={styles.container}>
      <AppBar style={{ backgroundColor: "#fff" }}>
        <ToolBar style={{ padding: 0 }}>
          <IconButton
            aria-label="search"
            color="inherit"
            style={{ paddingRight: 2 }}
            onClick={() => {
              props.navigation.navigate("Home");
            }}
          >
            <ArrowBack style={{ color: "#212121" }} />
          </IconButton>
          <InputBase
            className={classes.margin}
            inputProps={{ "aria-label": "naked" }}
            placeholder={"Search notes"}
            color={"secondary"}
            style={{
              fontWeight: 500,
              caretColor: "#b53d00",
              paddingLeft: 0,
              width: "100%",
            }}
            value={searchvalue}
            autoFocus={true}
            rows={10}
            onChange={(event) => {
              setSearch(event.target.value);
            }}
          />
          <IconButton
            aria-label="search"
            color="inherit"
            style={{}}
            onClick={() => {
              setSearch("");
            }}
          >
            <CloseIcon style={{ color: "#212121" }} />
          </IconButton>
        </ToolBar>
      </AppBar>
      <View
        style={{
          flex: 1,
          backgroundColor: "#fff3e",
          alignItems: "center",
          justifyContent: "flex-start",
           width:'100%',
          marginTop:40
        }}
      >
        {store.getState().filter(item=>
        {
          if(item.title )
          {
           return item.title.toLocaleLowerCase().startsWith(searchvalue.toLocaleLowerCase())
          }
          else{
            return item.content.toLocaleLowerCase().startsWith(searchvalue.toLocaleLowerCase())
          }
        
        }).map((item) =>         
         <View style={{}} key={item.id} >
             <TouchableWithoutFeedback
      style={{}}
      onPress={() => {
        props.navigation.navigate('Create',{id:item.id,title:item.title,content:item.content})
       
      }}
    >
      <View style={{ width: Platform.OS==='android'?Dimensions.get('screen').width-10: Dimensions.get('window').width-10 ,flex:1,margin:10,marginRight:10,marginLeft:10}}>
        <Card >
          <CardContent>
            <Typography
              variant="h6"
              component="h6"
              style={{
                fontSize: 15,
                fontWeight: "600",
                wordBreak: "break-all",
              }}
            >
             {item.title}
            </Typography>
            <Typography
              color="textSecondary"
              style={{ fontSize: 13, width: "100%", wordBreak: "break-all" }}
              display={"block"}
            >
              {item.content}
            </Typography>
            <Typography
              color="textSecondary"
              style={{ fontSize: 10, marginTop: "20%", marginBottom: 0 }}
              gutterBottom
            >
              {item.creation}
            </Typography>
          </CardContent>
        </Card>
      </View>
    </TouchableWithoutFeedback>
        </View>
        )
        }
      </View>
    </View>
  )
}

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
  },
  margin: {
    margin: theme.spacing(1),
  },
  notchedOutline: {
    borderWidth: "0px",
    borderColor: "white !important",
  },
}));
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff3",
    alignItems: "center",
    justifyContent: "center",
    width:"100%"
  },
});

