import React from "react";
import {
  StyleSheet,
  Text,
  View,
  Dimensions,
  ScrollView,
  FlatList,
  TouchableOpacity,
  TouchableWithoutFeedback,
  Platform,
} from "react-native";
import { Fab } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import NotesCard from "./../Crad/Card";
import AppBar from "@material-ui/core/AppBar";
import ToolBar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import SearchIcon from "@material-ui/icons/Search";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete";
import CloseIcon from "@material-ui/icons/Close";
import firebase from '../../store/firebase/MyFirebase'
import store, {
  ADD_CARD,
  DELETE_CARD,
  DESELECT_CARD,
} from "./../../store/CradsStore";
export default function Home(props) {
  const [componentstate, setcomponenstate] = React.useState(false);
  const [DATA, setDATA] = React.useState([]);
  const [height, setheight] = React.useState(Dimensions.get("window").height);
  const [isSelectionStarted, startSelection] = React.useState(false);
  let fab;
  React.useEffect(() => {
    if (Platform.OS === "android" || Platform.OS === "ios") {
      setheight(Dimensions.get("screen").height);
    } else {
      setheight(Dimensions.get("window").height);
    }

  },[]);

  store.subscribe(() => {
    setDATA(store.getState());
  });
  const [isselected, setSelected] = React.useState(false);
  const [selectedcount, sendselectcount] = React.useState(0);
  function cardSelected(param) {
    setSelected(true);
    let count = selectedcount + param;
    sendselectcount(count);
    if (count === 0) {
      startSelection(false);
    }
  }
  function cleardata() {
    store.dispatch(DESELECT_CARD());
  }
  let appbar = (
    <AppBar style={{ backgroundColor: "#fff" }}>
      <ToolBar>
        <Text style={{ color: "#212121" }}>Notes</Text>
        <IconButton
          aria-label="search"
          color="inherit"
          style={{ marginLeft: "auto" }}
          onClick={() => {
            props.navigation.navigate("SearchCard");
          }}
        >
          <SearchIcon style={{ color: "#212121" }} />
        </IconButton>
      </ToolBar>
    </AppBar>
  );
  if (!isselected) {
    appbar = (
      <AppBar style={{ backgroundColor: "#fff" }}>
        <ToolBar>
          <Text style={{ color: "#212121" }}>Notes</Text>
          <IconButton
            aria-label="search"
            color="inherit"
            style={{ marginLeft: "auto" }}
            onClick={() => {
              props.navigation.navigate("SearchCard");
            }}
          >
            <SearchIcon style={{ color: "#212121" }} />
          </IconButton>
        </ToolBar>
      </AppBar>
    );
  } else if (selectedcount > 0) {
    appbar = (
      <AppBar style={{ backgroundColor: "#fff" }}>
        <ToolBar style={{ padding: 0 }}>
          <IconButton
            aria-label="search"
            color="inherit"
            style={{}}
            onClick={() => {
              setSelected(false);
              sendselectcount(0);
              setcomponenstate(false);
              cleardata();
              startSelection(false);
            }}
          >
            <CloseIcon style={{ color: "#212121" }} />
          </IconButton>
          <Text style={{ color: "#212121", marginRight: "auto" }}>
            {selectedcount} Selected
          </Text>
          <IconButton
            aria-label="search"
            color="inherit"
            style={{ marginLeft: "auto" }}
            onClick={() => {
              store.dispatch(DELETE_CARD());
              setSelected(false);
              sendselectcount(0);
              startSelection(false);
            }}
          >
            <DeleteIcon style={{ color: "#212121" }} />
          </IconButton>
        </ToolBar>
      </AppBar>
    );
  }

  if (!isSelectionStarted) {
    fab = (
      <Fab
        size="large"
        color="secondary"
        aria-label="add"
        style={{
          position: "fixed",
          top: height / 1.15,
          left: Dimensions.get("window").width - 90,
          zIndex: 1,
          backgroundColor: "#b53d00",
        }}
        onClick={() => {
          props.navigation.navigate("Create");
        }}
      >
        <AddIcon fontSize={"default"} />
      </Fab>
    );
  }
  return (
    <View>
      {appbar}
      <View style={styles.container}>
        {fab}
        <ScrollView style={{ flexDirection: "column" }}>
          <View style={{ flexDirection: "row" }}>
            <View
              style={{
                flexDirection: "column",
                alignItems: "center",
                flex: 0.5,
                marginRight: 5,
                marginLeft: 10,
                marginTop: 10,
              }}
            >
              {DATA.map((item) => {
                if (item.id % 2 !== 0) {
                  return (
                    <View
                      key={item.id}
                      style={{ marginBottom: 10, width: "100%" }}
                    >
                      <NotesCard
                        val={item.id}
                        text={item.title}
                        select={cardSelected}
                        carddata={item}
                        start={startSelection}
                        startState={isSelectionStarted}
                        nav={props.navigation}
                      />
                    </View>
                  );
                }
              })}
            </View>
            <View
              style={{
                flexDirection: "column",
                alignItems: "center",
                flex: 0.5,
                marginLeft: 5,
                marginRight: 10,
                marginTop: 10,
              }}
            >
              {DATA.map((item) => {
                if (item.id % 2 === 0) {
                  return (
                    <View
                      key={item.id}
                      style={{ marginBottom: 10, width: "100%" }}
                    >
                      <NotesCard
                        val={item.id}
                        text={item.title}
                        key={item.id}
                        select={cardSelected}
                        carddata={item}
                        start={startSelection}
                        startState={isSelectionStarted}
                        route={props.route}
                        nav={props.navigation}
                      />
                    </View>
                  );
                }
              })}
            </View>
          </View>
        </ScrollView>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    marginTop: 50,
    display: "flex",
    backgroundColor: "#f0f4c",
    flexDirection: "row",
    width: "100%",
    height: "100%",
  },
});
