import { configureStore, createAction, createReducer } from "@reduxjs/toolkit";
export const ADD_CARD = createAction("cardAdded");
export const DELETE_CARD = createAction("cardDeleted");
export const SELECT_CARD = createAction("cardSelected");
export const DESELECT_CARD = createAction("cardDeselected");
export const UNSELECT_CARD = createAction("cardUnselected");
export const UPDATE_CARD = createAction("cardUpdated");
export const ADDING_CARD=createAction('cardAdding');
export const UPDATING_CARD=createAction('cardUpdating')
export const DELETING_CARD=createAction('cardDeleting')
import CreateCardMiddleWare from './CreateCardMiddleWare';
import DeleteCardMiddleWare from './DeleteCardMiddleWare';
import UpdateCardMiddleWare from './UpdateCardMiddleWare'
let reducer = createReducer([], {
  cardAdded: (state, action) => {
   return state;
  },
  cardDeleted: (state, action) => {
    return state;
  },
  cardSelected: (state, action) => {
    let index = state.findIndex((card) => card.id === action.payload.id);
    state[index].css.iselected = true;
    return state;
  },
  cardDeselected: (state, action) => {
    state.map((card) => {
      card.css.iselected = false;
    });
  },
  cardUnselected:(state, action) => {
    let index = state.findIndex((card) => card.id === action.payload.id);
    state[index].css.iselected = false;
    return state;
  },
  cardUpdated:(state,action)=>
  {
    let index = state.findIndex((card) => card.id === action.payload.id);
    state[index].title=action.payload.title;
    state[index].content= action.payload.content;
    state[index].creation=action.payload.creation;
    state[index].alaram=action.payload.alaram
    return state;
  },
  cardAdding:(state,action)=>
  {
    state.push({
      id: state.length+1,
      title: action.payload.title,
      content: action.payload.content,
      creation:action.payload.creation,
      alaram:action.payload.alaram,
      css: {
        iselected: false,
      },
    });
    return state.reverse();
  },
  cardDeleting:(state,action)=>
  {
    let newstate = state.filter((card) => card.css.iselected === false);
    return newstate;
  }
});
let store = configureStore({ reducer:reducer,middleware:[CreateCardMiddleWare,DeleteCardMiddleWare,UpdateCardMiddleWare]});

export default store;
