import React from "react";
import { StyleSheet, Text, View } from "react-native";
import Home from "./src/components/Home/Home";
import { NavigationContainer } from "@react-navigation/native";
import { createStackNavigator } from "@react-navigation/stack";
import CreateCard from "./src/components/CreateCard/CreateCard";
import search from "./src/components/Search/search";
import NotesCard from "./src/components/Crad/Card";
import Login from "./src/components/Login/Login";
import Register from "./src/components/Register/Register";
import ForgotPass from "./src/components/ForgotPassword/ForgotPassword";
import AuthStore,{LOGIN_SUCCESS,LOGIN_FAILURE} from "./src/store/AuthenticationStore";
import SplashScreen from './src/components/SplashScreen/SplashScreen'
import { AsyncStorage } from "react-native";
import firebase from './src/store/firebase/MyFirebase'
import store,{ADD_CARD,ADDING_CARD} from './src/store/CradsStore';
const Stack = createStackNavigator();
export default function App() {
  const [token, setToken] = React.useState(null);
  const [loogedin, setloggedIn] = React.useState(false);
  const [loading, setloading] = React.useState(true);  
  const [loader, setloader] = React.useState(true);
  React.useEffect(() => {
    AsyncStorage.getItem("token")
      .then(function (token) {
        if (token!=null || token!='') {
           firebase.auth().signInWithCustomToken(token).then(user=>
            {
              setloggedIn(true);
              setloading(false);
             firebase.database().ref('users/'+firebase.auth().currentUser.uid).once('value',function(snapshot)
              {
                setloading(false);
                AuthStore.dispatch(LOGIN_SUCCESS({
                  loggedIn: true,
                  Registered: false,
                  ValidUser: false,
                  loader: false,
                  Email: "",
                  Password: "",
                  Errors: "",
                  Token: "",
                }));
                snapshot.forEach(function(child)
                {
                    if(child.val()!="")
                    {
                      store.dispatch(ADD_CARD({ title: child.val().title, content: child.val().content, creation:child.val().creation,alaram:child.val().alaram}));
                    }           
                });             
              });     
            }).catch(err)
            {
              setloading(false)
                   setloggedIn(true);
              AuthStore.dispatch(LOGIN_FAILURE({
                loggedIn:false,
                Registered: false,
                ValidUser: false,
                loader: false,
                Email: "",
                Password: "",
                Errors: err.message,
                Token: "",
              }));
            }    
        }
        else{
          setloading(false)
          AuthStore.dispatch(LOGIN_FAILURE({
            loggedIn:false,
            Registered: false,
            ValidUser: false,
            loader: false,
            Email: "",
            Password: "",
            Errors: "",
            Token: "",
          }));

        }
      })
      .catch((err) => {
        setloading(false)
        AuthStore.dispatch(LOGIN_FAILURE({
          loggedIn:false,
          Registered: false,
          ValidUser: false,
          loader: false,
          Email: "",
          Password: "",
          Errors: "",
          Token: "",
        }));

      });
    // }
    // else
    // {setloading(false)
    //   AuthStore.dispatch(LOGIN_FAILURE({
    //     loggedIn:false,
    //     Registered: false,
    //     ValidUser: false,
    //     loader: false,
    //     Email: "",
    //     Password: "",
    //     Errors: "",
    //     Token: "",
    //   }));
    // }
  },[]);
  AuthStore.subscribe(() => {
    setloggedIn(AuthStore.getState().loggedIn);
    setloader(AuthStore.getState().loader);
  });
  if(!loading)
  {
        return(
    <NavigationContainer>
    {loogedin? (<Stack.Navigator
      screenOptions={{
        headerShown: false,
      }}
    >
      <Stack.Screen name="Home" component={Home} />
      <Stack.Screen name="MyCard" component={NotesCard} />
      <Stack.Screen name="Create" component={CreateCard} />
      <Stack.Screen name="SearchCard" component={search} />
    </Stack.Navigator>):(  <Stack.Navigator
      screenOptions={{
        headerShown: false,
      }}
    >
      <Stack.Screen name="Login" component={Login} />
      <Stack.Screen name="Register" component={Register} />
      <Stack.Screen name="ForgotPass" component={ForgotPass} />
    </Stack.Navigator>)}
  </NavigationContainer>)
  }
 else
  return (<NavigationContainer>
    <Stack.Navigator screenOptions={{
        headerShown: false,
      }}>
      <Stack.Screen name="Splash" component={SplashScreen}/>
    </Stack.Navigator>
  </NavigationContainer>
  );
      
}


const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff3e0",
    alignItems: "center",
    justifyContent: "center",
  },
});
