import {
  REGISTER_USER,
  LOGIN_USER,
  VALIDATE_USER,
  REGISTRATION_SUCCESS,
  REGISTRATION_FAILURE
} from "../store/AuthenticationStore";
import firebase from "./firebase/MyFirebase"
const RegisterTheUser = (store) => (next) => (action) => {
    let regState ={loggedIn:false,Registered:false,ValidUser:false,loader:false,Email:'',Password:'',Errors:'',Token:''}
  if (action.type != "RegisterUser") {
    return next(action);
  }
  next(action);
  if (action.payload.Email!='' && action.payload.Password!='') {
    firebase
      .auth()
      .createUserWithEmailAndPassword(
        action.payload.Email,
        action.payload.Password
      )
      .then((user) => {
      firebase.database().ref('users/'+user.user.uid).set({posts:""}).then(function()
        {
          user.user.sendEmailVerification().then(
              ()=>{  regState.Registered=true;             
              store.dispatch(REGISTRATION_SUCCESS(regState))
            }
          ).catch(err=>
              {
                  regState.Registered=false;
                  regState.Errors = err.message;
                  store.dispatch(REGISTRATION_FAILURE(regState))
              })
        }).catch(err=>
          {
            regState.Registered=false;
            regState.Errors = err.message;
            store.dispatch(REGISTRATION_FAILURE(regState))
          })
           
      }).catch((err) => {
        regState.Registered=false;
        regState.Errors = err.message;
        store.dispatch(REGISTRATION_FAILURE(regState))
      });
  } else {
    regState.Registered=false;
    regState.Errors = "Email and Password is Required";
    store.dispatch(REGISTRATION_FAILURE(regState))
  }
};
export default RegisterTheUser;
