import {
    REGISTER_USER,
    LOGIN_USER,
    VALIDATE_USER,
    LOGIN_SUCCESS,
    LOGIN_FAILURE,
    GET_TOKEN
  } from "../store/AuthenticationStore";
  import { AsyncStorage } from "react-native";
  import firebase from "./firebase/MyFirebase"
const LoginTheUser = (store)=>(next)=>(action)=>
{
    let regState ={loggedIn:false,Registered:false,ValidUser:false,loader:false,Email:'',Password:'',Errors:'',Token:'',uid:''}
      if(action.type!='loginUser')
      {
          return next(action)
      }
      next(action);
  firebase.auth().signInWithEmailAndPassword(action.payload.Email,action.payload.Password).then(function(userRecord)
  {
    if(userRecord.user.emailVerified)
    {
      regState.Token=userRecord.user.uid;
      store.dispatch(GET_TOKEN(regState))     
    }
    else{
      regState.Errors='Please Verify your Email Address';
       userRecord.user.sendEmailVerification().then(()=>
       {
        regState.Errors='Please Verify your Email Address';
        store.dispatch(LOGIN_FAILURE(regState))
       }).catch(err=>
        {
          regState.Errors=err.message;
          store.dispatch(LOGIN_FAILURE(regState))
        })
    }
  }).catch(err=>
    {
      regState.Errors=err.message;
      store.dispatch(LOGIN_FAILURE(regState))
    })
}
  export default LoginTheUser;