import React from "react";
import { StyleSheet, Text, View,Image } from "react-native";
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import Avatar from '@material-ui/core/Avatar';
import OutlinedTextField from '../../Helpers/OutlinedTextField'
import AuthStore from '../../store/AuthenticationStore';
import {LOGIN_USER} from '../../store/AuthenticationStore';
import MuiAlert from '@material-ui/lab/Alert'
import Snackbar from '@material-ui/core/Snackbar';
export default  function Login(props) {
  const[isSignedIn,setSignedIn]=React.useState(false)
  const[Email,setEmail]=React.useState('');
  const[Password,setPassword]=React.useState('');
  const [open, setOpen] = React.useState(false);
  function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }
  function LoggedInTheUser()
  {
    setSignedIn(true);
    AuthStore.dispatch(LOGIN_USER({loggedIn:false,Registered:false,ValidUser:false,loader:true,Email:Email,Password:Password,Errors:'',Token:''}))
  }
  AuthStore.subscribe(()=>
  {
    setSignedIn(AuthStore.getState().loader) 
    if(AuthStore.getState().Errors!='')
    {
      setOpen(true);
      console.log(AuthStore.getState().Errors)
    }
  })
  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };
  return (
    <View style={styles.container}>
      <View style={{width:80,height:80,marginBottom:40}}><Image source={require('../../../assets/brand.png')} style={{width:'100%',height:'100%'}} /></View> 
      <OutlinedTextField onChange={(event)=>setEmail(event.target.value)} id="outlined-basic1" type={'text'} label="Email" variant="outlined" size={'small'} style={{marginBottom:30,width:'70%'}} />
      <OutlinedTextField  onChange={(event)=>setPassword(event.target.value)} id="outlined-basic2" type={'password'} label="Password" variant="outlined"  size={'small'} style={{width:'70%'}} />  
      {isSignedIn? <CircularProgress style={{marginTop:20}} size={35} />: <Button variant="contained" color="primary" style={{width:'70%',marginTop:20}} onClick={()=>LoggedInTheUser()}>Login</Button>}  
      <Button color="primary" style={{marginTop:8}}  onClick={()=>{props.navigation.navigate('ForgotPass')}}>Forgot Password?</Button>
      <Button color="primary" onClick={()=>{props.navigation.navigate('Register')}}>New User?</Button>
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="error">
        {AuthStore.getState().Errors}
        </Alert>
      </Snackbar>
    </View>
  );
}
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
});
