import { configureStore, createAction, createReducer } from "@reduxjs/toolkit";
import RegisterTheUser from "./AuthMiddleWare";
import LoginTheUser from "./LoginMiddleWare";
import TokenUser from './TokenGenerateMiddleWare';
export const LOGIN_USER = createAction("loginUser")
export const REGISTER_USER = createAction("RegisterUser")
export const VALIDATE_USER = createAction("ValidateUser")
export const REGISTRATION_SUCCESS=createAction("RegSuccess")
export const REGISTRATION_FAILURE=createAction("RegFailure")
export const LOGIN_SUCCESS=createAction("LogSuccess")
export const LOGIN_FAILURE=createAction("LogFailure")
export const GET_TOKEN = createAction('Token')
let AuthReducer= createReducer({loggedIn:false,Registered:false,ValidUser:false,loader:false,Email:'',Password:'',Errors:'',Token:''},
    {
        loginUser:(state,action)=>
        {
            state.loader=true;
            state.Errors=''
            return state;
        },
        RegisterUser:(state,action)=>
        {
            state.loader=true;
            return state;
        },
        ValidateUser:(state,action)=>
        {
            return state;
        },
        RegSuccess:(state,action)=>
        {
            state.Registered=true;
            state.loader=false;
            return state;
        },
        RegFailure:(state,action)=>
        {
             state.Errors=action.payload.Errors,
             state.loader=false
            return state;
        },
        LogSuccess:(state,action)=>
        {
             state.loggedIn=true,
             state.loader=false
             return state;
        },
        LogFailure:(state,action)=>
        {
             state.Errors=action.payload.Errors,
             state.loader=false;
             state.loggedIn=false;
             return state;
        },
    })
    let AuthStore = configureStore({reducer:AuthReducer,middleware:[RegisterTheUser,LoginTheUser,TokenUser]})
    export default AuthStore;