import { AsyncStorage } from "react-native";
import firebase from "./firebase/MyFirebase";
import * as cardstore from "../store/CradsStore";
const CreateCardMiddleWare = (store)=>(next)=>(action)=>
{
    var  mystore = cardstore;
    if(action.type!='cardAdded')
    {
        return next(action);
    }
    next(action);
    let data ={
            id: cardstore.default.getState().length+1,
            title: action.payload.title,
            content: action.payload.content,
            creation:action.payload.creation,
            alaram:action.payload.alaram,
            css: {
              iselected: false,
            },         
            }
          firebase.database().ref('users/'+firebase.auth().currentUser.uid+'/posts'+data.id).set(data).then(function()
          {
              cardstore.default.dispatch(cardstore.ADDING_CARD(action.payload))
          })
   
 
}
  export default CreateCardMiddleWare;