import React from "react";
import { StyleSheet, Text, View, TextInput, Dimensions } from "react-native";
import AppBar from "@material-ui/core/AppBar";
import ToolBar from "@material-ui/core/Toolbar";
import ArrowBack from "@material-ui/icons/ArrowBack";
import Done from "@material-ui/icons/Done";
import IconButton from "@material-ui/core/IconButton";
import TextField from "@material-ui/core/TextField";
import InputBase from "@material-ui/core/InputBase";
import AlarmIcon from "@material-ui/icons/Alarm";
import { makeStyles } from "@material-ui/core/styles";
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import store, { ADD_CARD, DELETE_CARD,UPDATE_CARD,UPDATING_CARD } from "../../store/CradsStore";
import { KeyboardDatePicker } from "@material-ui/pickers";
export default function CreateCard(props) {
  const classes = useStyles();
  const [listcards, AddCard] = React.useState(props.route.params);
  const [CardTitle, setTitle] = React.useState(props.route.params?.title?props.route.params.title:"");
  const [Content, setContent] = React.useState(props.route.params?.content?props.route.params.content:"");
  const[isUpdating,setUpdating]= React.useState(props.route.params?.content?true:false)
  React.useEffect(() => {
    console.log( getCompletionDate())
  });
  function getLiveTimings()
  {
    return selectedDate.toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true })
  }
  function getTodayDate()
  {
    var d = new Date();
    return d.getDate()+'/'+selectedDate.getMonth()+"/"+selectedDate.getFullYear()
  }
  function getCompletionDate()
  {
    let month= ['January','February','March','April','May','June','July','August','September','October','November','December']
    return month[selectedDate.getMonth()]+" "+selectedDate.getDate()+', '+getLiveTimings()
  }
  const [selectedDate, handleDateChange] = React.useState(new Date());
  const[isdateclicked,setdateclicked]=React.useState(false);
  const[isAlaramModified,setAlaramModified]=React.useState(false);

  
  return (
    <View style={styles.container}>
       <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <KeyboardDatePicker
  value={selectedDate}
  placeholder="10/10/2018"
  minDate={new Date()}
  onChange={date => {handleDateChange(date);setdateclicked(false)}}
  format="MM/dd/yyyy"
  style={{display:'none'}}
  open={isdateclicked}
  autoOk={true}
   onAccept={()=>
   {
     setAlaramModified(true);
   }}
/>
</MuiPickersUtilsProvider>
      <AppBar
        style={{
          backgroundColor: "#fff",
          borderBottomWidth: 2,
          borderStyle: "solid",
          borderBottomColor: "#cfd8dc",
        }}
        elevation={0}
      >
        <ToolBar style={{ padding: 0 }}>
          <IconButton
            aria-label="search"
            color="inherit"
            style={{}}
            onClick={() => {
              props.navigation.navigate("Home");
            }}
          >
            <ArrowBack style={{ color: "#212121" }} />
          </IconButton>
          <IconButton
            aria-label="search"
            color="inherit"
            style={{ marginLeft: "auto" }}
            onClick={() => {
              setdateclicked(true)
            }}
          >
            <AlarmIcon style={{ color: "#212121" }} />
          </IconButton>
          <IconButton
            aria-label="search"
            color="inherit"
            style={{}}
            onClick={() => {
              if( isUpdating )
              {              
                store.dispatch(UPDATING_CARD({ id:props.route.params.id,title: CardTitle, content: Content,creation:getTodayDate()+' '+getLiveTimings(),alaram:isAlaramModified?getCompletionDate():props.route.params.alaram }));
                props.navigation.navigate("Home");
                return;
              }
              if(Content || CardTitle && !isUpdating )
              {
                console.log('ADDING Cntent',{ title: CardTitle, content: Content, creation:getTodayDate()+' '+getLiveTimings(),alaram:isAlaramModified?getCompletionDate():''})
                store.dispatch(ADD_CARD({ title: CardTitle, content: Content, creation:getTodayDate()+' '+getLiveTimings(),alaram:isAlaramModified?getCompletionDate():''}));
                props.navigation.navigate("Home");
                return;
              }      
            }}
          >
          <Done style={{ color: "#212121" }} />
          </IconButton>
        </ToolBar>
      </AppBar>
      <View
        style={{
          borderStyle: "solid",
          borderBottomWidth: 2,
          marginTop: 10,
          height: 80,
          borderBottomColor: "#cfd8dc",
        }}
      >
        <InputBase
          className={classes.margin}
          inputProps={{ "aria-label": "naked" }}
          placeholder={"Title"}
          color={"secondary"}
          style={{
            fontSize: 35,
            fontWeight: 500,
            paddingLeft: 8,
            caretColor: "#b53d00",
          }}
          autoFocus={true}
          rows={10}
          value={CardTitle}
          onChange={(event) => {
            setTitle(event.target.value);
          }}
        />
      </View>
      <View style={{ backgroundColor: "#fff" }}>
        <TextField
          id="filled-multiline-static"
          multiline
          rows={Dimensions.get("window").height / 23}
          variant="outlined"
          placeholder={"Content"}
          style={{
            backgroundColor: "#fff",
            borderRadius: 0,
            caretColor: "#b53d00",
          }}
          color={"secondary"}
          InputProps={{
            classes: {
              notchedOutline: classes.notchedOutline,
            },
          }}
          value={Content}
          onChange={(event) => {
            setContent(event.target.value);
          }}
        />
      </View>
    </View>
  );
}
const styles = StyleSheet.create({
  container: {
    marginTop: 50,
    display: "flex",
    backgroundColor: "#fff",
    flex: 1,
  },
});
const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
  },
  margin: {
    margin: theme.spacing(1),
  },
  notchedOutline: {
    borderWidth: "0px",
    borderColor: "white !important",
  },
}));
