import React from "react";
import { StyleSheet, Text, View,Image } from "react-native";
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import Avatar from '@material-ui/core/Avatar';
import OutlinedTextField from '../../Helpers/OutlinedTextField'
import AuthStore,{REGISTER_USER} from "../../store/AuthenticationStore";
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert'
export default  function Register(props) {
  const[isRegisterIn,setRegisterIn]=React.useState(AuthStore.getState().loader);
  const[Email,setEmail]=React.useState('');
  const[Password,setPassword]=React.useState('');
  const [open, setOpen] = React.useState(false);
  const [Success, setSuccess] = React.useState(false);
  function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }
  AuthStore.subscribe(()=>{
    setRegisterIn(AuthStore.getState().loader);
    console.log(AuthStore.getState())
    if(AuthStore.getState().Errors!='' && !AuthStore.getState().Registered )
    {
      setOpen(true);
    }
    if(AuthStore.getState().Registered)
    {
      setSuccess(true);
    }
  })
  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
    setSuccess(false);
  };
   function RegisterUser()
   {
    setRegisterIn(true)
     AuthStore.dispatch(REGISTER_USER({loggedIn:false,Registered:false,ValidUser:false,loader:true,Email:Email,Password:Password,Error:'',Token:''}))
   }
  return (
    <View style={styles.container}>
      <View style={{width:80,height:80,marginBottom:40}}><Image source={require('../../../assets/brand.png')} style={{width:'100%',height:'100%'}} /></View> 
      <OutlinedTextField onChange={(event)=>setEmail(event.target.value)} id="outlined-basic1" type={'text'} label="Email" variant="outlined" size={'small'} style={{marginBottom:30,width:'70%'}}   />
      <OutlinedTextField onChange={(event)=>setPassword(event.target.value)} id="outlined-basic2" type={'password'} label="Password" variant="outlined"  size={'small'} style={{width:'70%'}} />  
      <OutlinedTextField id="outlined-basic3" type={'password'} label="Confirm Password" variant="outlined"  size={'small'} style={{width:'70%',marginTop:30}} /> 
      {isRegisterIn? <CircularProgress style={{marginTop:20}} size={35} />: <Button variant="contained" color="primary" style={{width:'70%',marginTop:20}} onClick={()=>RegisterUser()}>Register</Button>}  
      <Button color="primary" style={{marginTop:8}}  onClick={()=>{props.navigation.navigate('Login')}}>Login</Button>
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="error">
        {AuthStore.getState().Errors}
        </Alert>
      </Snackbar>
      <Snackbar open={Success} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="success">
          Registration SuccessFull
        </Alert>
      </Snackbar>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
});
